body {
    font-family: 'Raleway';
    font-weight: 400;
    font-size: 18px;
    color: #000;
    background-color: #fff;
}
h1 {
    font-family: 'Raleway';
    font-weight: 900;
    font-size: 64px;
    color: #fff;
    margin: 0;
    box-sizing: border-box;
    padding: 0 15px;
}
h2 {

}
h3 {
    font-weight: 500;
    font-size: 30px;
}
h4 {
    font-weight: 900;
    color: #000;
    font-size: 24px;
}
.container {
    width: 1170px;
    position: relative;
    margin: 0 auto;
}
.landing {
    position: relative;
    background-image: url(../image/desk.png);
    background-repeat: no-repeat;
    background-size: cover;


}
.landing::before {
    content: "";
    position: absolute;
    top: 0; left: 0; right: 0; bottom: 0;
    // background: #252B49 ;
    // opacity: 0.8; 
}
.landing-main {
    text-align: center;
    color: #fff;
}
p {
    width: 1170px;
    text-align: center;
    font-size: 24px;
    box-sizing: border-box;
    padding: 0 10px;
    // margin: 0;
    text-align: center;
}